import React from 'react';
/*import './Middlebar.css';*/

/*
COLORS 
	Green
		#00FFA8
	Red
		#FF0000
	Yellow
		#FFC100
	Orange (device)
		#FFA200
*/
	

const clock_img = require('../images/clock_img.png');
const temp_img = require('../images/temp_img.png');
const lock_unlock_img = require('../images/lock_unlock_img.png');
const line_img = require('../images/line.png');
const home_btn_img = require('../images/HOME.png');
const away_btn_img = require('../images/AWAY.png');



/* General box for component 'WDay, Date and clock' and 'environmental' */
const GeneralInfoBox = (props) => {

	const s_img = {
		position: "relative",
		width: "20%",
		float: "left",
		left: "2vw",
	}

	const s_imageAndTextWrapper = {
		height: "100%",
		width: "100%",
		float: "right", 
		display: "flex", 
		flexDirection: "row",
		flexWrap: "wrap", 
		justifyContent: "center", 
		alignItems: "center", 
	}

	const s_texts = {
		position: "relative",
		width: "15vw",
		height: "auto", 
		textAlign: "center",
		fontWeight: "bold",
		margin: "0 auto",
		padding: "10px",
	}

	const s_smallText = {
		fontSize: "1vw",
	}

	const s_bigText = {
		fontSize: "4vw",
		width: "100%",
		color: props.middle_text_color
		

	}


	return(
		<div style={props.mainStyle}>

			<div style={s_imageAndTextWrapper}>

				<img style={s_img} src={props.img_src} alt={props.img_alt} />

				<div style={s_texts}>
					
					<div style={s_smallText}>{props.top_text}</div>
					<div style={s_bigText}>{props.middle_text}</div>
					<div style={s_smallText}>{props.bottom_text}</div>

				</div>

				
			</div>

		</div>
	)
}

/* System status, if system is armed or disarmed */
const SystemStatus = (props) => {

	const s_img = {
		position: "relative",
		width: "20%",
		float: "left",
		left: "2vw",
	}

	const s_imageAndTextWrapper = {
		height: "100%",
		width: "100%",
		float: "right", 
		display: "flex", 
		flexDirection: "row",
		flexWrap: "wrap", 
		justifyContent: "center", 
		alignItems: "center", 
	}

	const s_texts = {
		position: "relative",
		width: "10vw",
		height: "auto", 
		textAlign: "center",
		fontWeight: "bold",
		margin: "0 auto",
		padding: "10px",
		marginLeft: "2vw",
	}

	const s_smallText = {
		textAlign: "left",
		fontSize: "1vw",
		height: "1.5vw",
	}

	const s_bigText = {
		textAlign: "left",
		fontSize: "2.5vw",
		width: "100%",
		color: props.middle_text_color
		

	}


	return(
		<div style={props.mainStyle}>

			<div style={s_imageAndTextWrapper}>

				<img style={s_img} src={props.img_src} alt={props.img_alt} />

				<div style={s_texts}>
					
					<div style={s_smallText}>{props.top_text}</div>
					<div style={s_bigText}>{props.middle_text}</div>
					<div style={s_smallText}>{props.bottom_text}</div>

				</div>

				
			</div>

		</div>
	)
	
}

/* Change system status between armed (away) and disarmed (home) */
const ChangeStatus = (props) => {

	const s_left_box = {
		float: "left",
		height: "100%", 
		width: "49%",
		display: "flex",
		
	}

	const s_right_box = {
		float: "right", 
		height: "100%",
		width: "49%", 
		display: "flex",
	}


	const s_btn_img = {
		width: "50%",

		margin: "auto",

	}

	const s_line_div = {
		float: "left",
		height: "100%",
		width: "2%",
		display: "flex",

	}

	const s_line = {
		height: "6vw",
		margin: "auto",
	}


	return(
		<div style={props.mainStyle}>

			<div style={s_left_box}>
				<img src={props.home_img_src} alt={props.home_img_alt} style={s_btn_img}></img>
			</div>

			<div style={s_line_div}>
				<img src={props.sep_line_img_src} alt="line" style={s_line}></img>
			</div>

			

			<div style={s_right_box}>
				<img src={props.away_img_src} alt={props.away_img_alt} style={s_btn_img}></img>
			</div>

		</div>
	)
}




const Middlebar = () => {

	const s_middleBar = {
		marginTop: "1vw",
		width: "100vw",

		height: "10vw",
		color: "white"
	}

	const s_mainStyle = {
		backgroundColor: "#2E3442",
		marginLeft: "1vw",
		height: "10vw",
		width: "23.75vw",
		float: "left",
		borderRadius: "5px",
	}

	return(
		<div style={s_middleBar}>
			<GeneralInfoBox 
				mainStyle={s_mainStyle} 
				img_src={clock_img} 
				img_alt="clock" 
				top_text="Tuesday"
				middle_text="13.06"
				middle_text_color = "white"
				bottom_text="2.6.2020" 
			/>

			<SystemStatus 
				mainStyle={s_mainStyle} 
				img_src={lock_unlock_img} 
				img_alt="lock" 
				top_text="System" 
				middle_text="DISARMED" 
				middle_text_color="#00FFA8" 
			/>
			
			<ChangeStatus 
				mainStyle={s_mainStyle} 
				home_img_src={home_btn_img}
				home_img_alt="home"
				away_img_src={away_btn_img}
				away_img_alt="away"
				sep_line_img_src={line_img}
			
			/>

			
			
			<GeneralInfoBox 
				mainStyle={s_mainStyle} 
				img_src={temp_img} 
				img_alt="thermometer" 
				top_text="Current temperature" 
				middle_text="-26.6°C" 
				middle_text_color="#00E5FF" 
				bottom_text="last update 12.00" 
			/>
		</div>
	)
}

export default Middlebar;