import React from 'react';
import {useEffect, useState} from 'react';
import { unstable_batchedUpdates } from 'react-dom';
import {GeneralButton, DeviceButton} from './GeneralButton';


const plus_btn_img = require('../images/plus_img.png');
const battery75_img = require('../images/battery75_img.png');
const ok_img = require('../images/ok_img.png');
const warn_img = require('../images/warn_img.png');





var el_height;

const setElementHeight = (newHeight) => {
    el_height = newHeight;
}

const getElementHeight = () => el_height;


const Button = (props) => {

	const s_btnWrapper = {
		height: "100%",
		width: "100%",
        display: "flex",
		

	
	}

	const s_h3_btn_text = {
		color: "black",
        margin: "auto auto auto 1vw",
        fontSize: "1.25vw",
        float: "left",

    }

	const s_img_btn_image = {
        margin: "auto 1vw auto auto",
        width: "2vw",
        float: "right",
         
	}
	
	return(
		<div style={s_btnWrapper} onClick={props.call}>
			<p style={s_h3_btn_text}>{props.text}</p>
			<img style={s_img_btn_image} src={props.image} alt={props.image}></img>
		</div>
	)
}


const Device = (props) => {

    const s_device_box = {
        backgroundColor: "#FFA200",
        height: "3vw",
        width: "90%",
        margin: "1vw auto auto auto",
        borderRadius: "5px",

        //border: "solid black 7.5px",
    }

    const s_selected_device_box = {
        //border: "solid black 7.5px",
    }

    if (props.selected) {
        console.log("Laite on valittu")
        return(
            <div style={{...s_device_box, ...s_selected_device_box}}>
                <DeviceButton
                    text={props.text}
                    image={props.image}
                    on_bold={true}
                />
            </div>
        )
    } else {
        console.log("Laitetta ei ole valittu");
        return(
            <div style={s_device_box}>
                <DeviceButton
                    text={props.text}
                    image={props.image}
                    on_bold={false}
                />
            </div>
        )
    }

    

    
}

const Underline = () => {
    
    const s_line = {
        backgroundColor: "white",
        height: "1px", 
        width: "calc(100% - 2vw)",
        margin: "auto",
        float: "bottom",
    }

    return(
        <div style={s_line}></div>
    )
}

const HeaderBar = (props) => {

    const s_header_box = {
        //backgroundColor: "black",
        height: "3vw",
        width: "100%",

        position: "relative", 
		overflow: "hidden",
        display: "flex",
        alignItems: "center",
		justifyContent: "center",

    }

    const s_header = {
        //backgroundColor: "red",
        fontSize: "1.5vw",
        margin: "auto auto auto 1vw",
        fontWeight: "bold",
        width: "40%",
    }

    const s_add_device = {
        //backgroundColor: "blue",
        fontSize: "1vw",
        float: "right", 
        width: "5vw", 
    }

    const s_line = {
        backgroundColor: "white",
        height: "1px", 
        width: "calc(100% - 2vw)",
        margin: "auto",
        float: "bottom",
    }

    const s_plus_img = {
        margin: "auto 1vw auto 0.25vw",
        height: "1.5vw",
    }

    return(
        <div>
            <div style={s_header_box}>
                <p style={s_header}>
                    {props.title}
                </p>
                <p style={s_add_device}>
                    {props.text}
                </p>
                <img src={props.img_src} alt={props.img_alt} style={s_plus_img}></img>
            </div>
            <Underline />
        </div>
    )
}

const Devices = (props) => {

    const s_device_box = {
        backgroundColor: "#2E3442",
        marginLeft: "1vw",
        position: "relative",
        height: "100%", 
        width: "23.75vw",
        float: "left",
        borderRadius: "5px",
    }

    return(
            <div style={s_device_box}>
                <HeaderBar
                    title = {props.header_title}
                    text = {props.header_text}
                    img_src = {props.header_img_src}
                    img_alt = {props.header_img_alt}
                /> 

                <Device
                text="Front door"
                image={battery75_img}
                selected={true}
                />

                <Device
                text="Balcony door"
                image={battery75_img}
                selected={false}
                />


            </div>
    )
}

const TableRow = (props) => {
    const s_table_grid = {
        //backgroundColor: "blue",

        marginLeft: "1vw",

        fontSize: "1vw",
        width: "100%",

        height: "2vw",

        margin: "0.5vw 0 0.5vw 1vw"
    
    }

    const s_img_alerts = {
        height: "2vw"
    }


    return(
        <div style={s_table_grid}>
            <div style={{float: "left", width: "30%"}}>{props.first_row}</div>
            <div style={{float: "left", width: "25%"}}>{props.second_row}</div>
            <div style={{float: "left", width: "20%"}}>{props.third_row}</div>
            <div style={{float: "left", width: "20%", textAlign: "center"}}>{props.fourth_row}</div>
            <div style={{float: "left", width: "5%"}}>
                <img style={s_img_alerts} src={props.img_src}></img>
            </div>
        </div>     
    )
}

const Table = () => {

    const s_table = {
        //backgroundColor: "blue",

        height: "75%",
        width: "90%", 

        margin: "3vw auto auto auto",


        

    }
    return(
        <div style={s_table}>
            <TableRow
                first_row="Time"
                second_row="Device"
                third_row="Reason"
                fourth_row="Checked"
            />

            <Underline />
            
            <TableRow
                first_row="09.25 Tuesday 2.6.2020"
                second_row="Front door"
                third_row="Movement"
                fourth_row="checked"
                img_src={ok_img}
            />

            <TableRow
                first_row="11.56 Sunday 31.5.2020"
                second_row="Balcony door"
                third_row="Movement"
                fourth_row="unchecked"
                img_src={warn_img}
            />

        </div>

            
    )
}

const Alerts = (props) => {

    const s_alert_box = {
        backgroundColor: "#2E3442",
        position: "relative",
        height: "100%",
        width: "calc((23.75vw * 3) + 2vw)",  
        float: "right",
        marginRight: "1vw",
        borderRadius: "5px",
        //display: "flex",
    }

    return(
            <div style={s_alert_box}>
                <HeaderBar
                    title = {props.header_title}
                    text = {props.header_text}
                    img_src = {props.header_img_src}
                    img_alt = {props.header_img_alt}
                />

                <Table />
            </div>
    )
}

const Bottombar = () => {
    
    const [windowHeight, setWindowHeight] = useState(window.innerHeight);

    let resizeWindow = () => {
        console.log("@resize windows");
        setWindowHeight(window.innerHeight);
        setElementHeight(window.innerHeight);
    };

    useEffect(() => {
        resizeWindow();
        window.addEventListener('resize', resizeWindow);
        return () => {
            window.removeEventListener('resize', resizeWindow);
        }
    }, []);
    

    const s_main = {
        marginTop: "1vw",
        width: "100vw",
        height: "calc(" + windowHeight +"px - 18vw)",
        color: "white",

        
    }

    return(
        <div style={s_main}>
            <Devices
                header_title="Devices"
                header_text="add device"
                header_img_src={plus_btn_img}
                header_img_alt="plus sign"
                
            />

            <Alerts
                header_title="Alerts"
                header_text=""
                header_img_src={null}
                header_img_alt=""
                
            />

        </div>
    )
}

export default Bottombar;
